.w-card-container {
  /* padding: 1vw;
  margin: 2vw; */
  display: flex;
  /* width: 93vw; */
  background-color: #EDEDED;
  /* overflow: scroll */
  text-align: center;
}

.w-card {
  display: block;
  background: #FFF7E9;
  height: 55vh;
  min-width: 14vw;
  color: black;
  margin: 20px;
  padding: 20px;
  border-radius: 14px;
  /* overflow: hidden; */
  /* overflow-wrap: break-word; */

}

.carousel-start,
.carousel-end {
  cursor: pointer;
  position: absolute;
  top: 24vh;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  height: 68vh;
  opacity: 0.2;
  color: black;

}

.carousel-start {
  place-self: flex-start;
}

.carousel-end {
  right: 0;
}

.carousel-start:hover,
.carousel-end:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
}