.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


.card {
  display: inline-block;
  background: grey;
  height: 400px;
  width: 650px;
  color: white;
  margin: 10px;
  padding: 20px;
  border-radius: 14px;
  overflow: hidden;
  overflow-wrap: break-word;
}

.git-avatar {
  width: 155px;
  float: right;
  /* padding-right: 15px; */
  border-radius: 14px;
}

.card-container {
  display: flex;
  justify-content: space-evenly;
}


.accordion {
  width: 370px;
  margin: 2rem auto;
}

.accordion-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  background-color: #21aeca;
}

.accordion-title:hover {
  background-color: #3ab4cc;
}

.accordion-title,
.accordion-content {
  padding: 1rem;
}

.accordion-content {
  background-color: #39b9d2;
}
